.typeSelect{
  margin: 10px auto;
  width: 70%;
  height: 50px;
  position: sticky;
  display: flex;
  flex-direction: row;
  align-items: center;
  border-radius: 10px;
  padding: 10px;
  top: 72px;
  z-index: 1;
  background: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(10px);

  .select{
    flex: 1;
  }
}
