.login-form{
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	.login-item{
		width: 70%;

		.check{
			.check-item{
				margin: 0 5px;
			}
		}
		.img-code{
			width: 15%;
			margin: 0 40px;
		}

		.login-link{
			margin: 0 30px;
		}
		.login-form-button{
			width: 40%;
		}
	}
}
